/* ==========================================================================
   #Content bottom styles - begin
   ========================================================================== */

.eltd-content-bottom {
    
    .eltd-content-bottom-inner{
        box-sizing: border-box;
        
        &.eltd-grid{
            padding: 20px 0;
        }

    }
}
/* ==========================================================================
   #Content bottom styles - end
   ========================================================================== */

/* ==========================================================================
   Footer style - begin
   ========================================================================== */

.eltd-page-footer {
    @include eltdRelativeHolderLayout();
    z-index: 100;
    box-sizing: border-box;

    .widget a {
        &:hover {
            color:#ebebeb;
        }
    }

    .eltd-footer-top-holder {
        @include eltdRelativeHolderLayout();
        background-color: #363636;
        box-sizing: border-box;


        .eltd-footer-top-alignment-center{
            text-align: center;
        }
	    
        .eltd-footer-top-alignment-left{
            text-align: left;
        }
	    
        .eltd-footer-top-alignment-right{
            text-align: right;
        }

        .eltd-footer-top-inner{
            box-sizing: border-box;
	        
            &.eltd-grid{
                padding: 76px 0 48px 0;
            }
	        
            &.eltd-full-width{
                padding: 76px 50px;
            }

            .eltd-footer-top-custom-grid {
                max-width: 800px;

                @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                    margin: 0 auto;
                }

            }
        }
    }

    .eltd-footer-bottom-holder {
        @include eltdRelativeHolderLayout();
        background-color: #363636;
        box-sizing: border-box;
        text-align: center;

        .eltd-footer-bottom-inner {
            box-sizing: border-box;
            border-top: 1px solid #464646;
	        
            &.eltd-grid {
                padding: 37px 0 91px 0;
            }

            &.eltd-full-width {
                padding: 19px 50px;
            }


                ul.menu {
                    li{
                        display:inline;
                        font-size: 11px;
                        margin: 0 20px 0 20px;
                        font-family: "Open Sans";
                        text-transform: uppercase;
                    }
                }


        }
    }
}
/* ==========================================================================
   Footer style - end
   ========================================================================== */